.content-wrap{

    .articles_du_blog{
		h2{
			text-align: center;
			color: #000000;
		}

		.articles{
			background-color: #ffffff;
			@include cdm-grid(3, ".article", 30px, 30px, (			
				768: (
					col: 1
				)
			));

			.article{
				.content{
					padding: 0;
				}

				figure{
				&:hover{
					+ .description h3 a{
						background: linear-gradient(to right, #d8351f 0%,#f87a0a 100%);
						-webkit-background-clip: text;
						-webkit-text-fill-color: transparent;
						}
					}
				}
				.description{
					text-align: center;
				}
			}
		}

		.button-wrap{
			text-align: center;
		}
	}

	.articles{
		.article{

			figure{
				position:relative;

				

				a{
					display: block;

					&:before{
						content:'';
						position: absolute;
						top:0;
						right: 0;
						left:0;
						bottom:0;
						background:$color-gradient-orange;
						opacity: 0;
						@include hover-transition;
					}
	
					&:hover::before{
						opacity: 0.8;
					}
					
					opacity: 1;
					@include hover-transition;

					
				}
			}

			.description{
				//max-width: 50%;
				padding: 15px 0;
				h3{
					font-weight: 700;
					color: #000000;
					text-transform: uppercase;
					font-size:2.6rem;
					text-align:left;
					a{
						color: inherit;

						&:hover{
							background: linear-gradient(to right, #d8351f 0%,#f87a0a 100%);
							-webkit-background-clip: text;
							-webkit-text-fill-color: transparent;
						}
					}
				}
				.blog-categories{
					a{
						color: $color_pink;
						font-size: 1.6rem;
						@include hover-transition;

						&:hover{
							color: $color_blue;
						}
					}
				}
				p{
					font-size: 1.8rem;
					line-height: 1.3;
					text-align: left;
				
				}
			}
		}
	}
}
.home{
    .content-wrap{
        .articles_du_blog{
			margin-bottom:30px;
            .content{
                .articles{
                    .article{
                        display:flex;
                        flex-direction: column;
                        .description{
                            display:flex;
                            flex-grow: 1;
							flex-direction: column;
							

							@include media-screen(768px){
								width:80%;
								margin:0 auto;
							}
                            .readmore{
								margin-top:auto;
								text-align: left;
								
                            }
						}
						figure{
							
							@include media-screen(768px){
								width:80%;
								margin: 0 auto;
							}
						}
                    }
                }
            }
        }
    }
}
.page-blog{
	.content-wrap{
		.articles{
			width: 100%;

			.article{				
				background-color: #f5f5f5;

				&:nth-child(2n){
					.content{
						margin-left: auto;
					}
				}

				.content{
					display: flex;
					margin: 0;
					padding: 0;

					@media only screen and (max-width: 767px) {					
						display:block;		
                        text-align: center;
					}
				}

				figure{
					flex: 1 0 auto;
					max-width: 50%;
                    width:50%;
					@media only screen and (max-width: 767px) {
						width:100%;	
						max-width:100%;	
						display:block;			
					}
					&:hover{
						+ .description h3 a{
							background: linear-gradient(to right, #d8351f 0%,#f87a0a 100%);
							-webkit-background-clip: text;
							-webkit-text-fill-color: transparent;
						}
					}

				}

				.description{
					width: 100%;
					padding: 30px 20px;

					h3{
						text-align: left;
					}
                    @media only screen and (max-width: 767px) {
                        h3{
                            text-align: center;
                        }
                    }
				}

				&:nth-child(2n){
					background-color: #fff;

					figure{
						order: 1;
					}
					.description{
						order: 0;
					}
				}
			}
		}

		.col-wrapper{
			display: flex;

			.left-col{
				width: 100%;
				@media only screen and (max-width: 767px) {
					width:100%;				
				}
				display: flex;
				justify-content: end;
                flex-direction: column;
			}

			.right-col{
				@media only screen and (max-width: 767px) {
					display:none;		
				}
				width: 40%;
				flex: 1 1 auto;
				display: flex;
			}
		}
	}
    @media only screen and (max-width: 767px) {
        .content-wrap .content{
            background-color:#f5f5f5;  
        }
    }
}

.single-post{
	.content-wrap{
		section{
			h1{
				color: #000000;
				margin-bottom: 10px;
				margin-top:20px;
                text-align: center;
			}	

			.date{
				color: $color-pink;
                text-align: center;
			}
			.post-categories{
				text-align: center;
				font-style: italic;
				margin-bottom:0;
				margin-top:15px;
				a{
					&:after{
						content:'- ';
					}
				}
				a:last-child{
					&:after{
						content:'';
					}
				}
			}
			.content-blog{
                figure{
                    text-align: center;
                }
			}
			.blog-categories{
				a{
					color: $color_pink;
					font-size: 1.6rem;
					@include hover-transition;

					&:hover{
						color: $color_blue;
					}
				}
			}
		}
		.sharing{
			display:inline-block;
			text-align:center;
			width:100%;
			margin-bottom:20px;

			p{
				margin-bottom:1em;
			}

			i{
				font-size:2.5rem;
			}
		}
		.col-wrapper{
			display: flex;

			.left-col{
				max-width: 50%;
			}
			.right-col{
				padding-left: 30px;

				.content{
					padding-left: 0;
					padding-right: 0;
				}
			}

			@include media-screen(){
				flex-direction: column;

				.left-col{
					max-width: none;
				}

				.right-col{
					padding-left: 0;
				}
			}
		}
	}
}